.topicsHeader {
    text-align: center;
    margin-top: 15vh;
}

#topicsText {
    text-align: center;
}

.topicDiv {
    width: 25%;
    height: 7.5em;
    display: inline-block;

}

.folder {
    width: 50%;
}

.subtopicsRow {
    margin-top: 5em;
}

.resultsRow {
    height: 100vh;
    overflow: hidden;
    text-overflow: ellipses;
    margin-top: 3em;
}

#results {
    height: 75vh;
    overflow: auto;
}

.result {
    background-image: url(../../images/Card_Result_Active.svg);
    background-size: cover;
    background-repeat: none;
    display: inline-block;
    margin: 0.75em 0.5em 0.75em 0.5em;
    padding: 55px 25px 0 40px;
    color: black;
    height: 270px;
    width: 250px;
    vertical-align: top;
}

.result:hover {
    cursor: pointer;
}

.standard-serial {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.abstract-container {
    height: 7.5em;
    overflow: auto;
    text-overflow: ellipsis;
}

#subtopicNavigationContainer {
    background-color: rgba(0, 255, 255, 0.25);
    margin-top: 3em;
}

#chosenTopicImg {
    width: 7em;
    height: 7em;
    background-color: rgb(235, 139, 115);
    margin: 0 auto;
    transform: rotate(-10deg);
    margin-top: 2em !important;
    border-radius: 10px;
}

.z {
    z-index: 1;
}

#chosenTopic {
    background-color: white;
    color: rgb(235, 139, 115);
    border-radius: 1.5em;
    padding: 5px 0 5px 0;
}

#topicList {
    list-style: none;
    padding: 0;
    margin: 1em 0 3em 0;
}

#topicList li {
    display: inline-block;
    margin: 0.35em 0.4em 0.35em 0.4em;
    padding: 0 1em 0 1em;
    background-color: rgba(255, 255, 255, 0.15);
    border-radius: 15px;
    font-size: 18px;
    transition-duration: 1s;
}

#topicList li:hover {
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.15);
    transition-duration: 0.5s;
}

@media only screen and (max-width: 767px) {
    #topicsHeader {
        margin-top: 0;
    }
}